#somaDebito, #somaCredito {
  text-align: right;
  color: #fff;
  border: none;
  font-size: 15px;
  margin-top: 2px;
}

#somaDebito {
  background: #9e0000;
}

#somaCredito {
  background: #284270;
}

.lock-checkbox {
  width: 75px;
  margin-top: 10px;
  display: inline-block;
}

.lock-checkbox:last-child {
  padding-right: 20px;
  width: 95px;
}

.historico-variavel {
  border: solid 1px #d0d0d0;
  box-shadow: none;
  margin-bottom: 20px;
  border-radius: .29rem;
}

.historico-variavel .card-header {
  background: #f8f9ff;
  padding-top: 8px;
  padding-bottom: 5px;
}

.historico-variavel .card-header .lock-checkbox {
  padding-right: 0;
  margin: 0 10px;
  width: 75px;
}

.historico-variavel .card-body {
  padding: 0;
}

.historico-variavel .card-body [name="historicoVariavel"] {
  border: none;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 0 0 10px 10px;
}

.box-card {
  box-shadow: none;
  border: solid 1px #d0d0d0;
  margin-bottom: 20px;
}

.bloqueado span,
.bloqueado input.form-control {
  color: #f00;
}
